"use client"

import { useEffect, useState, useRef } from "react"
import { useRouter, useParams } from "next/navigation"
import { Button } from "@/components/ui/button"
import { ChatBubble } from "@/components/chat-bubble"
import { MessageInput } from "@/components/message-input"
import { ConversationList } from "@/components/conversation-list"
import { TypingIndicator } from "@/components/typing-indicator"
import { motion } from "framer-motion"
import { storage } from "@/lib/storage"
import { db } from "@/lib/supabase/db"
import { createClient } from "@/lib/supabase/client"
import type { AuthUser, Conversation, Message } from "@/lib/types"
import { v4 as uuidv4 } from "uuid"
import { ChevronLeft, MoreVertical } from "lucide-react"
import Link from "next/link"

export default function ChatPage() {
  const router = useRouter()
  const params = useParams()
  const conversationId = params.conversationId as string

  const [auth, setAuth] = useState<AuthUser | null>(null)
  const [conversation, setConversation] = useState<Conversation | null>(null)
  const [conversations, setConversations] = useState<Conversation[]>([])
  const [loading, setLoading] = useState(true)
  const [isTyping, setIsTyping] = useState(false)
  const [isSending, setIsSending] = useState(false)
  const messagesEndRef = useRef<HTMLDivElement>(null)
  const typingTimeoutRef = useRef<NodeJS.Timeout | null>(null)
  const supabase = createClient()

  useEffect(() => {
    const loadData = async () => {
      const authUser = await storage.getAuth()
      if (!authUser) {
        router.push("/login")
        return
      }
      setAuth(authUser)

      const conv = await storage.getConversationById(conversationId)
      if (!conv) {
        router.push("/dashboard")
        return
      }

      setConversation(conv)
      const convs = await storage.getConversationsByBusinessId(authUser.id)
      setConversations(convs)
      setLoading(false)
    }
    loadData()

    // Set up real-time subscription for new messages
    const channel = supabase
      .channel(`conversation:${conversationId}`)
      .on(
        "postgres_changes",
        {
          event: "INSERT",
          schema: "public",
          table: "messages",
          filter: `conversation_id=eq.${conversationId}`,
        },
        async (payload) => {
          // Reload conversation to get updated messages
          const updated = await storage.getConversationById(conversationId)
          if (updated) {
            setConversation(updated)
          }
        }
      )
      .subscribe()

    return () => {
      supabase.removeChannel(channel)
    }
  }, [conversationId, router, supabase])

  useEffect(() => {
    messagesEndRef.current?.scrollIntoView({ behavior: "smooth" })
  }, [conversation?.messages])

  const handleSendMessage = async (text: string) => {
    if (!conversation || !auth) return
    if (isSending) return // Prevent duplicate sends

    setIsSending(true)

    const tempId = uuidv4()
    const newMessage: Message = {
      id: tempId,
      conversationId: conversation.id,
      senderType: "business",
      senderId: auth.id,
      text,
      status: "sent",
      createdAt: new Date().toISOString(),
    }

    // Optimistically update UI immediately
    const optimisticConversation: Conversation = {
      ...conversation,
      messages: [...conversation.messages, newMessage],
      lastMessageAt: new Date().toISOString(),
    }
    setConversation(optimisticConversation)
    setIsTyping(false)

    try {
      // Send to database in background
      await db.createMessage(newMessage)
      
      // Update with actual data from database
      const updated = await storage.getConversationById(conversation.id)
      if (updated) {
        setConversation(updated)
      }
      const convs = await storage.getConversationsByBusinessId(auth.id)
      setConversations(convs)
    } catch (error) {
      console.error("Failed to send message:", error)
      // Revert optimistic update on error
      setConversation(conversation)
    } finally {
      setIsSending(false)
    }
  }

  const handleSendImage = async (imageUrl: string) => {
    if (!conversation || !auth) return
    if (isSending) return

    setIsSending(true)

    const tempId = uuidv4()
    const newMessage: Message = {
      id: tempId,
      conversationId: conversation.id,
      senderType: "business",
      senderId: auth.id,
      imageUrl,
      status: "sent",
      createdAt: new Date().toISOString(),
    }

    // Optimistically update UI immediately
    const optimisticConversation: Conversation = {
      ...conversation,
      messages: [...conversation.messages, newMessage],
      lastMessageAt: new Date().toISOString(),
    }
    setConversation(optimisticConversation)
    setIsTyping(false)

    try {
      await db.createMessage(newMessage)
      const updated = await storage.getConversationById(conversation.id)
      if (updated) {
        setConversation(updated)
      }
      const convs = await storage.getConversationsByBusinessId(auth.id)
      setConversations(convs)
    } catch (error) {
      console.error("Failed to send image:", error)
      setConversation(conversation)
    } finally {
      setIsSending(false)
    }
  }

  const handleTyping = () => {
    setIsTyping(true)
    if (typingTimeoutRef.current) {
      clearTimeout(typingTimeoutRef.current)
    }
    typingTimeoutRef.current = setTimeout(() => {
      setIsTyping(false)
    }, 3000) // Hide typing indicator after 3 seconds of inactivity
  }

  if (!auth || loading || !conversation) return null

  return (
    <main className="h-screen flex flex-col md:flex-row bg-background">
      {/* Sidebar */}
      <motion.aside
        initial={{ opacity: 0, x: -20 }}
        animate={{ opacity: 1, x: 0 }}
        className="hidden md:flex w-80 border-r border-border flex-col bg-card"
      >
        <div className="p-4 border-b border-border">
          <Link href="/dashboard">
            <h1 className="text-2xl font-bold text-primary cursor-pointer hover:opacity-80">Leenk</h1>
          </Link>
        </div>
        <div className="flex-1 overflow-y-auto">
          <ConversationList conversations={conversations} selectedId={conversationId} />
        </div>
      </motion.aside>

      {/* Chat Area */}
      <motion.div initial={{ opacity: 0 }} animate={{ opacity: 1 }} className="flex-1 flex flex-col">
        {/* Chat Header */}
        <div className="border-b border-border bg-card p-4 flex items-center justify-between">
          <div className="flex items-center gap-4">
            <Link href="/dashboard" className="md:hidden">
              <Button variant="ghost" size="sm">
                <ChevronLeft className="w-4 h-4" />
              </Button>
            </Link>
            <div>
              <h2 className="font-semibold">{conversation.customerEmail || "Guest Customer"}</h2>
              <p className="text-xs text-muted-foreground">Active now</p>
            </div>
          </div>
          <Button variant="ghost" size="sm">
            <MoreVertical className="w-4 h-4" />
          </Button>
        </div>

        {/* Messages */}
        <div className="flex-1 overflow-y-auto p-4 space-y-2 bg-gradient-to-b from-background to-card/30">
          {conversation.messages.length === 0 ? (
            <div className="flex items-center justify-center h-full text-muted-foreground">
              <p>No messages yet. Start the conversation!</p>
            </div>
          ) : (
            conversation.messages.map((msg, idx) => (
              <ChatBubble key={msg.id} message={msg} isOwn={msg.senderType === "business"} index={idx} />
            ))
          )}
          <div ref={messagesEndRef} />
        </div>

        {/* Message Input */}
        <MessageInput onSendMessage={handleSendMessage} onSendImage={handleSendImage} />
      </motion.div>
    </main>
  )
}
